<template>
  <nv-card>
    <div class="flex flex-wrap-reverse items-center mb-base">
      <vs-button class="p-3 mr-4" @click="cancel" color="primary" type="filled" icon="fa-chevron-left" icon-pack="far">{{ $t('BACK') }}</vs-button>
      <vs-button class="p-3 mr-4" @click="modify" color="primary" type="filled" icon="fa-pen" icon-pack="far" v-if="$acl.check('isReportAll')">{{ $t('MODIFY') }}</vs-button>
    </div>
    <div class="row hover">
      <p class="info-row">
        <span>{{ $t('ID') }}:</span>
        <span>{{ reportCategory.categoryId }}</span>
      </p>
      <p class="info-row">
        <span>{{ $t('CATEGORY_NAME') }}:</span>
        <span>
          <nv-localization-popup :active="popupActive" v-on:close="popupActive = false" path="/s1/report/reportCategory/localization" popupRef="localizationModal" :pkValue="this.$route.params.id" fieldName="categoryName"/>
          <vs-button class="m-0 p-0" color="primary" type="flat" @click="popupActive = true"><i class="far fa-globe-africa mr-1 text-primary"></i></vs-button>
          {{ reportCategory.categoryName }}
        </span>
      </p>
      <p class="info-row">
        <span>{{ $t('CREATION_DATE') }}:</span>
        <span>{{ reportCategory.creationDate | formatUnixTimestamp }}</span>
      </p>
    </div>
  </nv-card>
</template>

<script>
import ReportCategoryService from '@/services/report/ReportCategoryService'
import NvLocalizationPopup from '@/components/nv-localization/NvLocalizationPopup'

export default {
  name: 'report-category-view',
  components: {
    NvLocalizationPopup
  },
  data: function () {
    return {
      reportCategory: {},
      popupActive: false
    }
  },
  methods: {
    async load () {
      let self = this
      ReportCategoryService.reportCategories({
        categoryId: this.$route.params.id
      }).then(function (response) {
        self.reportCategory = response.data.reportCategoryList[0] || {}
      })
    },
    cancel () {
      this.$router.back()
    },
    modify () {
      this.$router.push({ name: 'ModifyReportCategory', params: { id: this.$route.params.id } })
    }
  },
  mounted () {
    this.load()
  }
}
</script>
